.container {
  background-image: radial-gradient(rgb(2, 2, 136), black);
  min-height: 100vh;
  width: 100%;
  margin: 0;
  padding: 0;
  position: relative;
  display: flex;
  justify-content: center;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
